                    </a-popconfirm>
                  </div>
                  <div>
                    <a-select ref="select" v-model:value="selectedLocal" style="width: 150px"
                      @focus="handleLoadLocalKeys()">
                      <a-select-option v-for="item in local.items" :key="item" :value="item.key"></a-select-option>
                    </a-select>
                    <a-button type="link" @click="handleOpenLocal(selectedLocal)">打开</a-button>
                    <a-button type="link" @click="handleUpdateLocal(selectedLocal)">更新</a-button>
                    <a-button type="link" @click="handleRemoveLocal(selectedLocal)">删除</a-button>

                    <input type="text" id="textfield" /><br />
                    <!-- <a class="a-upload"> -->
                    <input type="file" name="file" id="fileField"
                      onchange="document.getElementById('textfield').value=document.getElementById('fileField').value">
                    <!-- </a> -->

                    <!-- <a class="file">选择文件
  <input type="file" name="file" id="fileField1" onchange="document.getElementById('textfield').value=document.getElementById('fileField1').value">
</a> -->
                  </div>
                </a-space>
              </template>
            </a-popover>

            <a-tooltip placement="bottom" title="打开文件">
              <a-button @click="handleOpenFile()"><icon-font type="icon-wj" /></a-button>
              <input ref="inputFile" id="inputFile" type="file" style="display:none" @change="loadFile" />
            </a-tooltip>
            <!-- accept=".json" -->
            <a-tooltip placement="bottom" title="下载文件">
              <a-button @click="handleDownLoadFile()"><icon-font type="icon-xz" /></a-button>
            </a-tooltip>

            <a-popover placement="bottom" trigger="click">
              <a-tooltip placement="right" title="设置">
                <a-button><icon-font type="icon-sz" /></a-button>
              </a-tooltip>
              <template #content>
                <a-space direction="vertical">
                  <a-checkbox v-model:checked="showGrid" @change="handleChangeShowGrid">显示网格</a-checkbox>
                  <a-checkbox v-model:checked="showGrid" @change="handleChangeShowGrid">显示图片</a-checkbox>
                </a-space>
                <p>Content</p>
                <p>Content</p>
              </template>
            </a-popover>
          </a-space>
        </div>
        <!-- /Tool -->
        <!-- Content -->
        <a-layout-content ref="content" id="content"
          :style="{ background: '#fff', padding: '0px', margin: 0, minWidth: '0px', minHeight: '0px' }"
          @dragover="handleContentDragOver" @drop="handleContentDrop">

          <v-stage ref="stage" :config="stage.config" @mousedown="handleStageMouseDown" @touchstart="handleStageMouseDown"
            @mousemove="handleStageMouseMove" @mouseup="handleStageMouseUp" @click="handleStageClick"
            @dragstart="handleStageDragStart" @dragmove="handleStageDragMove" @dragend="handleStageDragEnd">
            <v-layer ref="layer" :config="stage.layer.config">
              <v-rect ref="selectionRectangle" :config="{ visible: false, fill: 'red', }" />
              <!-- <v-rect ref="selectionRectangle" id="selectionRectangle" width=0 height=0 visible=false fill='rgba(0,0,255,0.5)' /> -->

              <v-line v-for="(item, index) in stage.layer.grids" :key="index"
                :config="{ visible: this.showGrid, points: item.points, strokeWidth: item.strokeWidth, stroke: '#576075', tension: item.tension, }" />

              <v-group ref="shapeGroup" v-for="shape in stage.layer.shapes" :key="shape.name"
                @dragmove="handleGroupDragMove" @dragend="handleGroupDragEnd">

                <v-text v-if="shape.type == 'base-text'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-rect v-else-if="shape.type == 'base-line'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />     
                <v-rect v-else-if="shape.type == 'base-rect'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-ellipse v-else-if="shape.type == 'base-ellipse'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-rect v-else-if="shape.type == 'valve-rect'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-ellipse v-else-if="shape.type == 'valve-ellipse'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-shape v-else-if="shape.type == 'valve-gate'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-rect v-if="shape.type == 'pipe-line'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <v-rect v-else-if="shape.type == 'pipe-rect'" :config="shape" @mouseover="handleShapeMouseover"
                  @mouseout="handleShapeMouseout" @dragstart="handleShapeDragStart" @dragend="handleShapeDragEnd"
                  @transform="handleTransform" @transformend="handleTransformEnd" />
                <!-- +Math.sqrt(Math.pow(shape.width ,2) + Math.pow(shape.height ,2)) -->
                <v-label v-if="shape.type.includes('base')" />
                <v-label v-else-if="shape.type.includes('pipe')" />
                <v-label v-else-if="shape.type.includes('ellipse')"
                  :config="{ x: shape.x, y: shape.y - shape.height / 2, opacity: 0.75, offset: { x: 0, y: 0 } }">
                  <v-tag :config="{
                    fill: 'black', pointerDirection: 'down', pointerWidth: 10, pointerHeight: 10, lineJoin: 'round',
                    shadowColor: 'black', shadowBlur: 10, shadowOpacity: 0.5
                  }" />
                  <v-text
                    :config="{ text: shape.name, fontFamily: 'Calibri', fontSize: 15, padding: 5, fill: 'white' }" />
                </v-label>
                <v-label v-else :config="{ x: shape.x, y: shape.y, opacity: 0.75, offset: { x: 0, y: 0 } }">
                  <v-tag :config="{
                    fill: 'black', pointerDirection: 'down', pointerWidth: 10, pointerHeight: 10, lineJoin: 'round',
                    shadowColor: 'black', shadowBlur: 10, shadowOpacity: 0.5
                  }" />
                  <v-text
                    :config="{ text: shape.name, fontFamily: 'Calibri', fontSize: 15, padding: 5, fill: 'white' }" />
                </v-label>

                <v-image v-if="shape.type.includes('ellipse')"
                  :config="{ image: this.lockImg, visible: !shape.draggable, x: shape.x - 10, y: shape.y - 12, width: 20, height: 20, offset: { x: 0, y: 0 } }" />
                <v-image v-else
                  :config="{ image: this.lockImg, visible: !shape.draggable, x: shape.x + shape.width / 2 - 10, y: shape.y + shape.height / 2 - 12, width: 20, height: 20, offset: { x: 0, y: 0 } }" />

              </v-group>

              <v-transformer ref="transformer" :config="stage.transformer.config" />
              <v-transformer ref="selectionTransformer" :config="stage.selectionTransformer.config" />
            </v-layer>
          </v-stage>
        </a-layout-content>
        <!-- /Content -->

        <!-- Footer -->
        <a-layout-footer :style="{ paddingTop: '12px', textAlign: 'center', height: '49px' }">
          <a-button :style="{ fontWeight: '800' }" @click="handlePlusStageScale">+</a-button>
          <a-dropdown :trigger="['click']">
            <a-button :style="{ fontWeight: '800' }">{{ Math.round(stageScale * 100) }}%</a-button>
            <template #overlay>
              <a-menu @click="handleChangeStageScale">
                <a-menu-item key="20">20%</a-menu-item>
                <a-menu-item key="50">50%</a-menu-item>
                <a-menu-item key="80">80%</a-menu-item>
                <a-menu-item key="100">100%</a-menu-item>
                <a-menu-item key="120">120%</a-menu-item>
                <a-menu-item key="150">150%</a-menu-item>
                <a-menu-item key="180">180%</a-menu-item>
                <a-menu-item key="rest">重置</a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
          <a-button :style="{ fontWeight: '800' }" @click="handleMinusStageScale">-</a-button>

          <a-select ref="select" v-model:value="stageScale" :style="{ width: '120px', showArrow: 'false' }"
            @change="handleChangeStageScale">
            <a-select-option value="20">20%</a-select-option>
            <a-select-option value="50">50%</a-select-option>
            <a-select-option value="80">80%</a-select-option>
            <a-select-option value="100">100%</a-select-option>
            <a-select-option value="120">120%</a-select-option>
            <a-select-option value="150">150%</a-select-option>
            <a-select-option value="180">180%</a-select-option>
            <a-select-option value="rest">重置</a-select-option>
          </a-select>
          <!-- Shape [ name：{{ this.selectedShapeName }}
          type：{{ this.selectedShapeType }}
          count：{{ this.stage.layer.shapes.length }}
          postion：{{ this.selectedShapePostionTxt }}
          rotation：{{ this.selectedShapeRotation }}
          scale：{{ this.selectedShapeScaleTxt }} ]
          Stage [ scale：{{ this.stageScale }}
          postion：{{ this.stagePostionTxt }} ]
          mousePostionTxt [ {{ this.mousePostionTxt }} ] -->
        </a-layout-footer>
        <!-- /Footer -->

      </a-layout>

      <!-- Sider -->
      <a-layout-sider width="180px" style="background: #fff">
        <a-card class="cardRight" title="属性" :style="{ width: '100%', height: '100%', textAlign: 'center' }">
          <a-space v-show="!stageSelected" direction="vertical">
            <template #extra><a href="#">more</a></template>
            <a-input v-model:value="textShapeName" addon-before="名称" style="width: 150px"
              @keyup.enter="handleChangeShapeName()" /><!-- @keyup.enter -->
            <!-- <a-input-number v-show="!lineSelected" v-model:value="selectedShapeWidth" addon-before="字体"
                style="width: 150px" @change="handleChangeShapeWidth()" /> -->
            <div>
              <a-input-number v-show="!lineSelected" v-model:value="selectedShapeWidth" addon-before="宽度"
                style="width: 150px" @change="handleChangeShapeWidth()" />
              <a-input-number v-show="lineSelected" v-model:value="selectedShapeWidth" addon-before="长度"
                style="width: 150px" @change="handleChangeShapeWidth()" />
            </div>
            <div>
              <a-input-number v-show="!lineSelected" v-model:value="selectedShapeHeight" addon-before="高度"
                style="width: 150px" @change="handleChangeShapeHeight()" />
              <a-input-number v-show="lineSelected" v-model:value="selectedLineHeight" addon-before="粗细"
                style="width: 150px" @change="handleChangeLineHeight()" />
            </div>
            <a-input-number v-model:value="selectedShapeRotation" addon-before="旋转" style="width: 150px"
              @change="handleChangeShapeRotate()" />
            <a-switch v-show="!selectedShapeType.includes('base')" @click="handleChangeShapeState()"
              v-model:checked="selectedShapeOpen" checked-children="开" un-checked-children="关" />
          </a-space>
        </a-card>
      </a-layout-sider>
      <!-- /Sider -->
    </a-layout>
  </a-layout>
</template>

<script>
const width = 7000;
const height = 3700;

import { saveAs } from 'file-saver';
import { defineComponent, reactive, toRefs, } from 'vue';
import { message } from 'ant-design-vue';
import { createFromIconfontCN } from '@ant-design/icons-vue';
const IconFont = createFromIconfontCN({
  scriptUrl: 'http://at.alicdn.com/t/c/font_3863139_zxdvykgv8m.js',
});

export default defineComponent({
  components: {
    IconFont,
  },
  setup() {
    const state = reactive({
      siderSelectedKeys: ['1'],
      selectedShapeOpen: "false",
    })
    return {
      ...toRefs(state),
    }
  },
  name: "App",
  data() {
    return {
      lockImg: new Image(),

      stg: null, lyr: null, tr: null,
      selectionTr: null, selectionRect: null,
      idxShape: 0,
      MIN_WIDTH: 0, MAX_WIDTH: 100,
      MIN_HEIGHT: 0, MAX_HEIGHT: 100,
      STROKE_WIDTH: 3, STROKE_COLOR: "black",
      START_SIZE: { width: 50, height: 50 },
      fontSize: 20,
      stage: {
        config: {
          width: width,
          height: height,
          draggable: false,
        },
        layer: {
          config: {},
          grids: [],
          shapes: [],
        },
        transformer: {
          config: {
            keepRatio: false,// 移动边缘时保持比率（默认值为 true）
            flipEnabled: false,// 在变换时翻转/镜像形状（默认值为 True）
            centeredScaling: false,// 相对于中心调整大小（默认值为 false）
            ignoreStroke: true,// 忽略笔画大小（默认值为 false）
            resizeEnabled: true,// 大小
            rotateEnabled: true,// 旋转
            rotationSnaps: [0, 45, 90, 135, 180, 225, 270, 315],// 旋转吸附角度（默认值为 []）
            //borderStroke: "black",
            borderDash: [4, 4],// 边框破折号数组
            borderStrokeWidth: 2,
            anchorSize: 10,// 锚角大小（默认值为 10）
            anchorCornerRadius: 5,// 锚角半径
            padding: 5,//内边距（默认值为 0）
            //enabledAnchors: ['top-left', 'top-center', 'top-right', 'middle-right', 'middle-left', 'bottom-left', 'bottom-center', 'bottom-right'],// 已启用句柄的名称数组
            boundBoxFunc: (oldBoundBox, newBoundBox) => {
              if (newBoundBox.width < this.MIN_WIDTH || newBoundBox.height < this.MIN_HEIGHT) {
                return oldBoundBox;
              }
              return newBoundBox;
            },// 边界框功能
          },
        },
        selectionTransformer: {
          config: {
            keepRatio: false,// 移动边缘时保持比率（默认值为 true）
            flipEnabled: false,// 在变换时翻转/镜像形状（默认值为 True）
            centeredScaling: false,// 相对于中心调整大小（默认值为 false）
            ignoreStroke: true,// 忽略笔画大小（默认值为 false）
            resizeEnabled: true,// 大小
            rotateEnabled: true,// 旋转
            rotationSnaps: [0, 45, 90, 135, 180, 225, 270, 315],// 旋转吸附角度（默认值为 []）
            //borderStroke: "black",
            borderDash: [4, 4],// 边框破折号数组
            borderStrokeWidth: 2,
            anchorSize: 10,// 锚角大小（默认值为 10）
            anchorCornerRadius: 5,// 锚角半径
            padding: 5,//内边距（默认值为 0）
          },
        },
      },

      serchName: "",
      textShapeName: "",
      selectedShapeName: "",
      selectedShapeType: "",
      selectedShapeWidth: 0,
      selectedShapeHeight: 0,
      selectedLineWidth: 100,
      selectedLineHeight: 5,
      selectedShapeRotation: 0,
      selectedShapeScaleTxt: "",
      selectedShapePostionTxt: "",
      mousePostionTxt: "",
      stagePostionTxt: "",
      stageScale: 1,
      fileName: "",
      dragImgName: "",
      userList: [],
      btnLockType: "icon-unlock",
      btnLockTitle: "锁定",
      showGrid: true,
      shapeSelected: false,
      lineSelected: false,
      stageSelected: true,
      saveLocalName: "",
      saveLocalCover: false,

      draggedShape: null,
      renderComponent: true,

      local: { items: [{ key: "" }] },
      selectedLocal: "",
      gridSize: 15,

      filePath: "",
      x1: 0,
      y1: 0,
      x2: 0,
      y2: 0,
    }
  },
  beforeCreate() {
  },
  created() {
    setTimeout(() => {
      this.handleResize();
    })
    setTimeout(() => {
      this.handleResize();
    })
    window.addEventListener("resize", this.handleResize);
    window.addEventListener("keydown", this.handleKeydown);
    window.addEventListener("keyup", this.handleKeyup);
  },
  beforeMount() {
  },
  mounted() {
    this.lockImg.src = require('@/assets/images/lock.png');

    this.width = window.document.getElementById("content").offsetWidth;
    this.height = window.document.getElementById("content").offsetHeight;
    this.stg = this.$refs.stage.getStage();
    this.lyr = this.$refs.layer.getNode();
    this.tr = this.$refs.transformer;
    this.selectionTr = this.$refs.selectionTransformer;
    this.selectionRect = this.$refs.selectionRectangle;//.getNode()
    //this.selectionRect = window.document.getElementById("selectionRectangle");

    const gridSize = this.gridSize;
    const gridCount = 5;

    for (var i = 0; i <= width / gridSize; i++) {
      this.stage.layer.grids.push({
        points: [i * gridSize, 0, i * gridSize, height],
        strokeWidth: 0.5,
      });
      this.stage.layer.grids.push({
        points: [0, i * gridSize, width, i * gridSize],
        strokeWidth: 0.5,
      });
    }

    for (var j = 0; j <= (width / gridSize) * gridCount; j++) {
      this.stage.layer.grids.push({
        points: [j * gridSize * gridCount, 0, j * gridSize * gridCount, height],
        strokeWidth: 1,
        tension: 1,
      });

      this.stage.layer.grids.push({
        points: [0, j * gridSize * gridCount, width, j * gridSize * gridCount],
        strokeWidth: 1,
        tension: 1,
      });
    }
  },
  computed: {},
  watch: {},
  methods: {
    test() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;
      if (shape.type === "base-line") {
        //shape.width=1500;
        // alert(shape.x())
        // console.log(shape.width + " " + shape.x+ " " + shape.y+ " " + shape.points[0]+ " " + shape.points[1]+" "+shape.points[2]+" "+shape.points[3])
        //  shape.points=[0,0,500,shape.y];
        shape.points[3] = 0;
      }
    },
    handleChangeStageScale(e) {
      //alert(e.key)
      if (e.key === "rest") {
        this.handleResetStage();
      } else {
        this.stg.scale({ x: e.key / 100, y: e.key / 100 });
      }
      this.stg.batchDraw();
      this.updateText();
    },
    handlePlusStageScale() {
      if (this.stageScale > 1.8) return;
      this.stg.scale({ x: this.stageScale + 0.1, y: this.stageScale + 0.1 });
      this.stg.batchDraw();
      this.updateText();
    },
    handleMinusStageScale() {
      if (this.stageScale < 0.3) return;
      this.stg.scale({ x: this.stageScale - 0.1, y: this.stageScale - 0.1 });
      this.stg.batchDraw();
      this.updateText();
    },
    handleChangeShowGrid(e) {
      this.showGrid = e.target.checked;
    },
    toPage(n) {
      if (this.$route.name !== n) {
        //获取路由对象并切换
        this.$router.replace({
          name: n,
        })
      }
    },
    handleChangeLayer(e) {
      const arr = this.stage.layer.shapes;
      const shape = arr.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;
      const index = arr.indexOf(shape);
      switch (e.key) {
        case "up":
          if (index == arr.length - 1) return;
          this.swapItems(arr, index, index + 1);
          break;
        case "down":
          if (index == 0) return;
          this.swapItems(arr, index, index - 1);
          break;
        case "forward":
          arr.push(arr.splice(index, 1)[0]);
          break;
        case "bottom":
          arr.unshift(arr.splice(index, 1)[0]);
          break;
      }
    },
    handleImgDragStart(e) {
      //alert(e.target.name)
      this.dragImgName = e.target.name;
    },
    handleContentDragOver(e) {
      e.preventDefault();
    },
    handleContentDrop(e) {
      //e.preventDefault();

      this.stg.setPointersPositions(e);

      const mousePos = this.stg.getPointerPosition();
      // make getScaledPointerPosition()
      var mx = (mousePos.x - this.stg.x()) / this.stg.scaleX();
      var my = (mousePos.y - this.stg.y()) / this.stg.scaleY();

      this.dragAddShape(this.dragImgName, Math.round(mx), Math.round(my));
    },
    updateTransformer(name) {
      const trNode = this.tr.getNode();

      const selectedNode = this.stg.findOne("." + name);
      if (!selectedNode) {
        this.shapeSelected = false;
        trNode.nodes([]);
        return;
      }
      if (selectedNode === trNode.node()) return;

      const shape = this.stage.layer.shapes.find((s) => s.name === name);

      if (shape.type.includes("line") || shape.type === "base-text") {
        this.stage.transformer.config.enabledAnchors = ['middle-left', 'middle-right'];// 已启用句柄的名称数组
      } else {
        this.stage.transformer.config.enabledAnchors = ['top-left', 'top-center', 'top-right', 'middle-right', 'middle-left', 'bottom-left', 'bottom-center', 'bottom-right'];// 已启用句柄的名称数组
      }
      this.shapeSelected = true;
      trNode.nodes([selectedNode]);

      trNode.getLayer().batchDraw();
    },
    handleTransform(e) {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      shape.x = e.target.x();
      shape.y = e.target.y();
      shape.scaleX = e.target.scaleX();
      shape.scaleY = e.target.scaleY();
      shape.width = e.target.width();
      if (shape.type === "base-line") {
        shape.height = 3;
      } else {
        shape.height = e.target.height();
      }
      shape.rotation = e.target.rotation();

      this.updateText();
    },
    handleTransformEnd() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      shape.x = Math.round(shape.x);
      shape.y = Math.round(shape.y);
      // 跳过笔划大小调整的第一种方法是重置缩放，并设置宽度、高度
      shape.width = Math.round(shape.width * shape.scaleX);
      shape.height = Math.round(shape.height * shape.scaleY);
      // shape.width = Math.min(shape.width * shape.scaleX, this.MAX_WIDTH)
      // shape.height = Math.min(shape.height * shape.scaleY, this.MAX_HEIGHT)
      shape.scaleX = 1;
      shape.scaleY = 1;
      shape.rotation = Math.round(shape.rotation);

      this.updateText();
    },
    handleKeydown(e) {
      //e.preventDefault();

      if (e.keyCode === 17) {
        this.stage.config.draggable = true;
        // stage.container().style.cursor = 'move'
        document.getElementById("content").style.cursor = 'move';
        return;
      }

      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      if (e.keyCode === 46) {
        this.handleRemoveShape();
      }

      if (!shape.draggable) return;

      const offset = 4;
      if (e.keyCode === 37) {
        shape.x = shape.x - offset;
      } else if (e.keyCode === 38) {
        shape.y = shape.y - offset;
      } else if (e.keyCode === 39) {
        shape.x = shape.x + offset;
      } else if (e.keyCode === 40) {
        shape.y = shape.y + offset;
      } else {
        return;
      }

      //e.preventDefault();
    },
    handleKeyup(e) {
      //e.preventDefault();

      if (e.keyCode === 17) {
        this.stage.config.draggable = false;
        document.getElementById("content").style.cursor = 'default';
      }

      //e.preventDefault();
    },
    handleResize() {
      const content = document.getElementById("content");
      if (!content) return;
      this.stage.config.width = content.offsetWidth;
      this.stage.config.height = content.offsetHeight;
    },
    dragAddShape(shapeType, mx, my) {
      this.idxShape++;
      this.shapeSelected = false;
      var named = "shape" + this.idxShape;
      const isSame = this.stage.layer.shapes.find((s) => s.name === named);
      if (isSame) {
        named = named + "(2)";
      }
      switch (shapeType) {
        case "base-line":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: null,
            x: mx, y: my, width: this.START_SIZE.width * 2, height: 3, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "base-text":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: 'black',
            x: mx, y: my, width: this.START_SIZE.width, height: this.fontSize, scaleX: 1, scaleY: 1, rotation: 0,
            text: named, fontSize: this.fontSize,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "base-rect":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: null,
            x: mx, y: my, width: this.START_SIZE.width, height: this.START_SIZE.height, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "base-ellipse":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: 'rgb(0,0,0,0)',
            x: mx, y: my, width: this.START_SIZE.width, height: this.START_SIZE.height, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "valve-rect":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: "green",
            x: mx, y: my, width: this.START_SIZE.width, height: this.START_SIZE.height, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "valve-ellipse":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: "green",
            x: mx, y: my, width: this.START_SIZE.width, height: this.START_SIZE.height, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "valve-gate":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: "green",
            x: mx, y: my, width: this.START_SIZE.width, height: this.START_SIZE.height, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
            sceneFunc(context, shape) {
              context.beginPath();
              context.moveTo(0, 0);
              context.lineTo(shape.getAttr('width'), shape.getAttr('height'));
              context.lineTo(shape.getAttr('width'), 0);
              context.lineTo(0, shape.getAttr('height'));
              context.closePath();
              context.fillStrokeShape(shape);
            },
          });
          break;
        case "pipe-line":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: "green",
            x: mx, y: my, width: this.START_SIZE.width * 2, height: 3, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: "green", strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
        case "pipe-rect":
          this.stage.layer.shapes.push({
            name: named, type: shapeType, fill: "green",
            x: mx, y: my, width: this.START_SIZE.width * 2, height: this.START_SIZE.height / 5, scaleX: 1, scaleY: 1, rotation: 0,
            stroke: this.STROKE_COLOR, strokeWidth: this.STROKE_WIDTH,
            draggable: true, strokeScaleEnabled: false,
          });
          break;
      }
      this.textShapeName = this.selectedShapeName = named;
      this.updateLock(true);

      setTimeout(() => {
        this.updateTransformer(this.selectedShapeName);
        if (shapeType === "base-line") {
          this.lineSelected = true;
        } else {
          this.lineSelected = false;
        }
      })

      this.updateText();
    },
    jsonAddShape(name, type, fill, x, y, width, height, scaleX, scaleY, rotation, stroke, strokeWidth, draggable, strokeScaleEnabled) {
      this.shapeSelected = false;
      if (type === "valve-gate") {
        this.stage.layer.shapes.push({
          name: name, type: type, fill: fill,
          x: x, y: y, width: width, height: height, scaleX: scaleX, scaleY: scaleY, rotation: rotation,
          stroke: stroke, strokeWidth: strokeWidth,
          draggable: draggable, strokeScaleEnabled: strokeScaleEnabled,
          sceneFunc(context, shape) {
            context.beginPath();
            context.moveTo(0, 0);
            context.lineTo(shape.getAttr('width'), shape.getAttr('height'));
            context.lineTo(shape.getAttr('width'), 0);
            context.lineTo(0, shape.getAttr('height'));
            context.closePath();
            context.fillStrokeShape(shape);
          },
        });
      } else if (type === "base-text") {
        this.stage.layer.shapes.push({
          name: name, type: type, fill: fill,
          x: x, y: y, width: width, height: height, scaleX: scaleX, scaleY: scaleY, rotation: rotation,
          text: name, fontFamily: 'Calibri', fontSize: this.fontSize, draggable: draggable, strokeScaleEnabled: strokeScaleEnabled,
        });
      } else {
        this.stage.layer.shapes.push({
          // offset: {x: 25,y: 25},
          name: name, type: type, fill: fill,
          x: x, y: y, width: width, height: height, scaleX: scaleX, scaleY: scaleY, rotation: rotation,
          stroke: stroke, strokeWidth: strokeWidth,
          draggable: draggable, strokeScaleEnabled: strokeScaleEnabled,
        });
      }

      this.updateText();
    },
    handleChangeShapeName() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      if (shape.name === this.textShapeName) return;

      if (shape.type === "base-text") {
        shape.name = shape.text = this.textShapeName;
      } else {
        const shape2 = this.stage.layer.shapes.find((s) => s.name === this.textShapeName);
        if (shape2) {
          alert("名称已存在！");
          this.textShapeName = this.selectedShapeName;
          return;
        }
        shape.name = this.textShapeName;
        this.textShapeName = this.selectedShapeName = shape.name;
      }

      this.updateText();
    },
    handleChangeShapeWidth() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      shape.width = this.selectedShapeWidth;
      this.updateText();
    },
    handleChangeShapeHeight() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      shape.height = this.selectedShapeHeight;
      this.updateText();
    },
    handleChangeShapeRotate() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;

      if (shape.type.includes("ellipse")) {
        shape.rotation = this.selectedShapeRotation;
      } else {
        const displayedWidth = shape.width * shape.scaleX;
        const displayedHeight = shape.height * shape.scaleY;
        const topLeft = { x: -displayedWidth / 2, y: -displayedHeight / 2 };
        const current = this.rotatePoint(topLeft, shape.rotation);
        const rotated = this.rotatePoint(topLeft, this.selectedShapeRotation);
        const dx = rotated.x - current.x;
        const dy = rotated.y - current.y;

        shape.rotation = this.selectedShapeRotation;
        shape.x = shape.x + dx;
        shape.y = shape.y + dy;
      }
      this.updateText();
    },
    rotatePoint({ x, y }, deg) {
      const degToRad = Math.PI / 180;
      const rcos = Math.cos(deg * degToRad);
      const rsin = Math.sin(deg * degToRad);
      return { x: x * rcos - y * rsin, y: y * rcos + x * rsin };
    },
    handleChangeShapeState() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (!shape) return;
      if (shape.type.includes("base")) return;

      if (this.selectedShapeOpen) {
        shape.fill = "red";
      } else {
        shape.fill = "green";
      }

      if (shape.type === "pipe-line") {
        shape.stroke = shape.fill;
      }

      this.updateText();
    },
