    handleStageMouseDown(e) {
      if (e.target === e.target.getStage()) {
        this.selectedShapeName = "";
        this.selectedShapeType = "base";
        this.shapeSelected = false;
        this.stageSelected = true;
        this.updateTransformer(this.selectedShapeName);
        return;
      }

      const clickedOnTransformer = e.target.getParent().className === "Transformer";
      if (clickedOnTransformer) {
        return;
      }

      const name = e.target.name();
      const shape = this.stage.layer.shapes.find((s) => s.name === name);
      if (shape) {
        this.stageSelected = false;
        this.shapeSelected = true;
        this.textShapeName = this.selectedShapeName = name;
        this.updateLock(shape.draggable)
        if (shape.type === "base-line") {
          this.lineSelected = true;
        } else {
          this.lineSelected = false;
        }
      } else {
        this.stageSelected = true;
        this.shapeSelected = false;
        this.textShapeName = this.selectedShapeName = "";
      }
      this.updateTransformer(this.selectedShapeName);
      this.updateText();
    },
