    updateLock(draggable) {
      if (draggable) {
        this.stage.transformer.config.resizeEnabled = true;
        this.stage.transformer.config.rotateEnabled = true;
        this.btnLockType = "icon-unlock";
        this.btnLockTitle = "锁定";
      } else {
        this.stage.transformer.config.resizeEnabled = false;
        this.stage.transformer.config.rotateEnabled = false;
        this.btnLockType = "icon-lock";
        this.btnLockTitle = "解锁";
      }
    },
    stageZeroPostion() {
      this.stg.position({ x: 0, y: 0 });
      this.updateText();
    },
    positionShape() {
      const shape = this.stg.findOne("." + this.serchName);
      if (shape) {
        //this.stg.position({ x: shape.position().x * -1, y: shape.position().y * -1, })
        this.stg.position({ x: shape.position().x * -1 + width / 2, y: shape.position().y * -1 + height / 2 });
        this.updateTransformer(this.serchName);
        this.selectedShapeName = this.serchName;
      } else {
        alert("输入图形名称错误！");
      }
      this.updateText();
    },
    updateText() {
      const shape = this.stage.layer.shapes.find((s) => s.name === this.selectedShapeName);
      if (shape) {
        this.selectedShapeName = shape.name;
        this.selectedShapeType = shape.type;
        this.selectedShapeWidth = shape.width;
        this.selectedShapeHeight = shape.height;
        this.selectedShapeRotation = shape.rotation;
        this.selectedShapePostionTxt = "x: " + shape.x + "  y: " + shape.y;
        this.selectedShapeScaleTxt = "x: " + shape.scaleX + "  y: " + shape.scaleY;

        if (shape.fill === "red") {
          this.selectedShapeOpen = true;
        }
        if (shape.fill === "green") {
          this.selectedShapeOpen = false;
        }
      }

      const mousePos = this.stg.getPointerPosition();
      var mx = (mousePos.x - this.stg.x()) / this.stg.scaleX();
      var my = (mousePos.y - this.stg.y()) / this.stg.scaleY();
      this.mousePostionTxt = "x: " + mx.toFixed() + "  y: " + my.toFixed();

      this.stageScale = this.stg.scaleX();
      this.stagePostionTxt = "x: " + this.stg.position().x + "  y: " + this.stg.position().y;
    },
    handleResetStage() {
      this.stg.scaleX(1);
      this.stg.scaleY(1);
      this.stg.position({ x: 0, y: 0 });
      this.handleResize();
      this.updateText();
    },
    //交换元素
    //数组，元素1，元素2
    swapItems(arr, index1, index2) {
      arr[index1] = arr.splice(index2, 1, arr[index1])[0];
      return arr;
    },
    handleOpenLocal(localkey) {
      const data = localStorage.getItem(localkey) || '[]';

      try {
        this.stage.layer.shapes.length = 0;
        this.userList = JSON.parse(data);
        this.userList.forEach((item) => {
          this.jsonAddShape(
            item.name, item.type, item.fill,
            item.x, item.y, item.width, item.height, item.scaleX, item.scaleY, item.rotation,
            item.stroke, item.strokeWidth,
            item.draggable, item.strokeScaleEnabled);
        });

        const trNode = this.tr.getNode();
        trNode.nodes([]);
        this.selectedShapeName = null;
        this.idxShape = this.stage.layer.shapes.length;

        setTimeout(() => {
          this.handleResize();
        })
        setTimeout(() => {
          this.handleResize();
        })
      } catch (error) {
        message.error('打开失败，请检查文件内容！');
      }
      message.success('打开成功！');
      //this.stage.layer.shapes = JSON.parse(data);
    },
    handleUpdateLocal(localkey) {
      if (localkey === "") return;
      localStorage.setItem(localkey, JSON.stringify(this.stage.layer.shapes));
    },
    handleRemoveLocal(localkey) {
      localStorage.removeItem(localkey);
      this.selectedLocal = "";
      this.stage.layer.shapes.length = 0;
    },
    handleSaveLocal() {
      if (this.saveLocalName === "") return;

      const key = this.local.items.find((i) => i.key === this.saveLocalName);
      if (key) {
        this.saveLocalCover = true;
        return;
      } else {
        this.saveLocalCover = false;
        localStorage.setItem(this.saveLocalName, JSON.stringify(this.stage.layer.shapes));
        this.saveLocalName = "";
      }
    },
    handelSaveLocalCoverConfirm() {
      localStorage.setItem(this.saveLocalName, JSON.stringify(this.stage.layer.shapes));
      this.saveLocalName = "";
      this.saveLocalCover = false;
      message.success('覆盖保存成功！');
    },
    handelSaveLocalCoverCancel() {
      this.saveLocalCover = false;
    },
    handleLoadLocalKeys() {
      this.local.items.length = 0;
      for (var i = 0; i < localStorage.length; i++) {
        this.local.items.push({ key: localStorage.key(i) });
      }
    },
    handleDownLoadFile() {
      var data = JSON.stringify(this.stage.layer.shapes);
      let str = new Blob([data], { type: 'text/plain;charset=utf-8' });
      saveAs(str, `导出文件时的名字.json`);
    },
    handleOpenFile() {
      this.fileName = '';
      this.$refs.inputFile.value = '';
      this.$refs.inputFile.dispatchEvent(new MouseEvent('click'));
    },
    loadFile(e) {
      if (window.FileReader) {
        const file = e.target.files[0];

        const reader = new FileReader();
        reader.readAsText(file);
        //监听文件读取结束后事件
        reader.onloadend = evt => {
          if (!reader.result) {
            return;
          }
          var fileObj = document.getElementById("inputFile").files[0];
          this.filePath = fileObj.path;
          //console.log(this.filePath);
          alert(e.target.result)
          //alert(document.getElementById('inputFile').value)
          try {
            this.stage.layer.shapes.length = 0;
            this.userList = JSON.parse(evt.target.result);
            this.userList.forEach((item) => {
              this.jsonAddShape(
                item.name, item.type, item.fill,
                item.x, item.y, item.width, item.height, item.scaleX, item.scaleY, item.rotation,
                item.stroke, item.strokeWidth,
                item.draggable, item.strokeScaleEnabled);
            });

            const trNode = this.tr.getNode();
            trNode.nodes([]);
            this.selectedShapeName = null;
            this.idxShape = this.stage.layer.shapes.length;
            this.fileName = file.name;

            setTimeout(() => {
              this.handleResize();
            })
            setTimeout(() => {
              this.handleResize();
            })
          } catch (error) {
            message.error('导入失败，请检查文件内容！');
          }
        }
      } else {
        alert("FileReader Not supported by your browser!");
      }
    },
  },
});
</script>
